@import "../StyleVariables/variables.scss";

.btn{
    background-color: #E4E4E4;
    background-color: $lgGrey;
    border-radius: 4px;
    border-width: 0px;
    box-shadow: #2d234266 0 2px 4px,
    #2d234266 0 7px 13px -3px,
    #9898a2 0 -3px 0 inset;
    cursor: pointer;
    height: 30px;
    padding-left: 10px;
    padding-right: 10px;
    transition: .2s;
    margin: 5px;
    font-family: sarpanch;
    color: #575873;
    font-size: 12px;
}
.btn h4{
    margin-top: 4px;
    text-shadow: 0px 0px 1px  #7d83f8;
}

.btn:active{
    box-shadow: #9898a2 0 0 0 1.5px inset;
    color: #B5EC1B;
    
}
.btn h4:active{
    margin-top: 5px;
}

.btn-container{
    // margin-right: 2vw;
    background-color: #00000022; // dark mode
    background-color: $btnshellpurple; // dark mode (do not use)
    // background-color: $cloudBlue; // light mode
    border-radius: 8px;
    border: #951515;
    border-width: 10px;
    box-shadow: 5px 3px 5px #0d0d0ddc 1 1 1 1.5px inset,
    0px 5px 18px white;
}